@charset "UTF-8";
:root {
  --background-header: #4c4a4b;
  --background-nav: linear-gradient(135deg, var(--nav-bg-start), var(--nav-bg-end));
  --color: whitesmoke;
  --hightlight-color: #f587f5;
  --facebook: rgba(30, 30, 241, 0.838);
  --twitter: hsl(203, 89%, 53%);
  --america: hsl(318, 66%, 53%);
  --america-hover: rgb(52, 234, 52);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --dark-bg: hsl(230, 17%, 14%);
  --text-color: whitesmoke;
  --light-bg: #ffc0cb;
  --text-color-light: #000;
  --nav-bg-start: rgb(71, 201, 249);
  --nav-bg-end: rgb(234, 132, 215);
  --nav-bg-dark: rgb(159, 144, 144);
  --nav-bg-dark-end: #322b2b;
  --background-list: whitesmoke;
  --background-dropdown: whitesmoke;
}

@media (prefers-color-scheme: dark) {
  :root {
    --background-header: var(--dark-bg);
    --color: var(--text-color);
    --toggle-bg: linear-gradient(225deg,var(--toggle-bg-end) 0%,var(--toggle-bg-start) 98.02%);
    --toggle-button: var(--dark-bg);
    --background-nav: linear-gradient(135deg, var(--nav-bg-dark) 0%, var(--nav-bg-dark-end) 97.5%);
    --text-color-light: var(--text-color);
    --background-list: #000;
    --background-dropdown: #000;
    --facebook: rgb(38, 162, 238);
  }
}
body.light {
  --background-header: var(--light-bg);
  --color: var(--text-color-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --background-nav: linear-gradient(135deg, var(--nav-bg-start), var(--nav-bg-end));
  --text-color-light: var(--nav-bg-dark-end);
  --background-list: var(--text-color);
  --background-dropdown: whitesmoke;
  --facebook: rgb(17, 104, 226);
}

:root {
  --font-inter: "Inter", sans-serif;
}

a, a:visited, a:active {
  text-decoration: none;
}

*, *::before, *::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style-type: none;
}

.visually-hidden {
  position: absolute;
  top: 0;
  left: -10000px;
  overflow: hidden;
}

.header {
  background: var(--background-header);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 1rem;
}
.header__language {
  font-size: 1.125rem;
  position: relative;
}
.header__language .header__button {
  cursor: pointer;
  border: 1px solid white;
  padding: 0.25rem;
  background: #bdbaba;
  border-radius: 0.75rem;
}
.header__language .button__control {
  border: none;
  background: none;
  font-size: 1rem;
  cursor: pointer;
  color: white;
}
.header__language .button__control:hover {
  color: greenyellow;
}
.header__language .header__link {
  position: absolute;
  border: 1px solid #000;
  padding: 0.375rem;
  background: whitesmoke;
  border-radius: 0.75rem;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s ease-in-out;
}
.header__language .header__link:hover {
  color: #4ff938;
  background: whitesmoke;
  border: 1px solid whitesmoke;
}
.header__language:hover .header__link {
  opacity: 1;
  visibility: visible;
}
.header__pages {
  display: flex;
  gap: 0.5rem;
  font-size: 1.25rem;
  transform: scale(1.2);
}
.header__pages .page__facebook {
  color: var(--facebook);
}
.header__pages .icon--twitter {
  color: var(--twitter);
}
.header__pages .icon--youtube {
  color: var(--youtube);
}
.header .toggle {
  display: grid;
  grid-template-columns: auto auto;
  border: none;
  align-items: center;
  gap: 0.5rem;
}
.header .toggle label {
  font-size: 0.875rem;
  font-weight: 400;
  color: var(--color);
}
.header .toggle label[for=dark] {
  margin: 0 0.5rem 0.15625rem;
}
.header .toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.header .toggle__wrapper input[type=radio] {
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
  width: 1.5rem;
  height: 1.5rem;
}
.header .toggle__wrapper:focus ~ .toggle__button {
  border: 2px solid white;
}
.header .toggle__background {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background: var(--toggle-bg);
  border-radius: 0.75rem;
  pointer-events: none;
}
.header .toggle__button {
  position: absolute;
  right: 100%;
  top: 0.1875rem;
  left: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  background: var(--toggle-button);
  border-radius: 50%;
  transition: all 150ms ease-in-out;
}
.header .toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
}
.header .toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.navbar {
  position: relative;
  background: var(--background-nav);
  margin: 1.5rem 0.875rem;
  border-radius: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  height: 600px;
}
.navbar__image {
  width: 200px;
  height: auto;
  margin: 1.25rem 1.25rem;
}
.navbar__list {
  display: flex;
  align-items: center;
  gap: 1.125rem;
  padding: 0.875rem;
  margin: 1.25rem 2.5rem auto auto;
  background: var(--background-list);
  border: 1px solid var(--color);
  border-radius: 0.75rem;
  font-size: 1.25rem;
}
@media (max-width: 58.75em) {
  .navbar__list {
    display: grid;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: repeat(2, auto);
  }
}
.navbar__list li {
  display: inline-block;
  position: relative;
}
.navbar__list a {
  display: block;
}
.navbar__list ul.dropdown li {
  display: block;
  text-decoration: underline;
}
.navbar__list ul.dropdown li:hover {
  text-decoration: none;
}
.navbar__list ul.dropdown {
  position: absolute;
  width: 200px;
  height: auto;
  padding: 0.5rem 1rem;
  z-index: 1;
  line-height: 1.5;
  background-color: var(--background-dropdown);
  margin-top: 0.875rem;
  border-top: 2px solid var(--america);
  overflow: hidden;
  opacity: 0;
  visibility: hidden;
  transition: 0.8s ease-in-out;
}
.navbar__list a {
  color: var(--text-color-light);
}
.navbar__list a:hover, .navbar a:focus-visible {
  color: var(--nav-bg-end);
}
.navbar__list li.dropdown__control:hover ul.dropdown {
  visibility: visible;
  opacity: 1;
}
.navbar__home, .navbar__list {
  position: sticky;
  top: 0;
  z-index: 999;
}

.info {
  position: absolute;
  top: 26%;
  left: 5%;
  line-height: 1.6;
  letter-spacing: 0.0625rem;
}
@media (max-width: 58.75em) {
  .info {
    top: 40%;
  }
}
@media (max-width: 39.8125em) {
  .info {
    top: 60%;
  }
}
.info .info__title {
  font-size: 3.125rem;
  background: linear-gradient(45deg, #de7171, #e95de9);
  /* Màu gradient */
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  display: inline-block;
}
@media (max-width: 58.75em) {
  .info .info__title {
    font-size: 1.875rem;
  }
}
@media (max-width: 39.8125em) {
  .info .info__title {
    font-size: 1.25rem;
  }
}
.info__detail {
  font-size: 1.875rem;
  display: block;
  font-weight: 900;
}
.info__detail .marketing {
  background: linear-gradient(45deg, #7a1a7a, orange 80%);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  display: inline-block;
}
.info__description {
  color: #333;
  font-size: 1.25rem;
  margin: 0.75rem auto;
}
.info__description .description--cut {
  display: block;
}
.info__link {
  color: white;
  background: linear-gradient(45deg, #098dc1, 60%, #f417de);
  border-radius: 2.5rem;
  padding: 1rem;
  font-size: 1rem;
  margin-top: 1rem;
  position: relative;
  z-index: 1;
  border: none;
  cursor: pointer;
  overflow: hidden;
}
.info__link::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 0%;
  background: linear-gradient(45deg, #f417de 2.5%, #098dc1);
  transition: 0.5s ease;
  z-index: -1;
}
.info__link:hover:before {
  width: 100%;
}

.count {
  display: flex;
  justify-content: space-around;
}
.count__number {
  padding: 0.75rem;
  text-align: center;
  line-height: 1.6;
  color: whitesmoke;
  background: linear-gradient(45deg, #9f9c9c, #ebcc93);
  width: 18.75rem;
  height: 120px;
  border-radius: 0.75rem;
}
.count__number .num {
  display: block;
  font-size: 2.5rem;
  background: linear-gradient(45deg, pink, aqua 2.5%);
  background-clip: text;
  --webkit-background-clip: text;
  color: transparent;
  display: inline-block;
}
.count__number .text {
  display: block;
  font-size: 1.75rem;
}
.company {
  padding: 4rem 2rem;
  line-height: 1.4;
}
.company .company__title {
  font-size: 1.5rem;
  background: linear-gradient(45deg, #f632f6, #eda828);
  background-clip: text;
  --webkit-background-clip: text;
  color: transparent;
  display: inline-block;
}
.company .intro {
  margin-top: 0.75rem;
}
.company .intro__description {
  display: flex;
  justify-content: space-around;
}
.company .intro__title {
  font-size: 3.75rem;
  font-weight: bolder;
  line-height: 0.8;
}
@media (max-width: 58.75em) {
  .company .intro__title {
    font-size: 2.8125rem;
  }
}
@media (max-width: 39.8125em) {
  .company .intro__title {
    font-size: 1.875rem;
  }
}
.company .intro__detail {
  color: #686464;
  font-size: 1.125rem;
  width: 50%;
}
.company .intro .content {
  display: flex;
  justify-content: space-around;
  margin-top: 4rem;
}
.company .intro .content__image {
  width: 500px;
  height: auto;
  box-shadow: -20px -20px 0px rgba(202, 85, 202, 0.2);
}
@media (max-width: 58.75em) {
  .company .intro .content__image {
    width: 400px;
    height: auto;
  }
}
@media (max-width: 39.8125em) {
  .company .intro .content__image {
    width: 250px;
    height: auto;
  }
}
.company .intro .content__contact {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.company .intro .content__contact .contact__info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 2rem;
}
.company .intro .content__contact .contact__info .learn--more {
  width: 12.5rem;
}
.company .intro .content__contact .contact__info .us {
  font-size: 1.25rem;
}
.company .intro .content__contact .contact__info .phone {
  display: block;
  font-size: 1.5rem;
}
.company .intro .content__contact .contact__info .phone:hover {
  color: #2deb14;
}
.company .intro .content__group {
  padding: 1rem;
  background-color: whitesmoke;
  border-top-left-radius: 4rem;
}
.company .intro .content__title {
  color: #000;
  font-size: 1.75rem;
  font-weight: bold;
}
.company .intro .content__wrap {
  font-size: 1.125rem;
  color: gray;
}

.service {
  margin-top: 1rem;
  background: rgba(161, 127, 140, 0.1);
  padding: 4rem;
  line-height: 1.6;
}
.service__header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 2rem;
}
.service__title {
  background: linear-gradient(45deg, #ea57ea, #faab19 67.5%);
  background-clip: text;
  --webkit-background-clip: text;
  color: transparent;
  display: inline-block;
  margin-bottom: 1rem;
}
.service__head {
  font-size: 3.125rem;
  font-weight: bold;
}
@media (max-width: 58.75em) {
  .service__head {
    font-size: 2.25rem;
  }
}
@media (max-width: 39.8125em) {
  .service__head {
    font-size: 1.5rem;
  }
}
.service__group {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, auto);
  /* Các lớp dịch vụ riêng biệt */
}
.service__group .service__item {
  position: relative;
  background: transparent;
  padding: 2rem 1.5rem;
  border-right: 1px solid purple;
}
.service__group .service__item .service__plus {
  width: 50px;
  height: 50px;
  background: rgba(228, 165, 176, 0.2);
  border-radius: 4rem;
  box-shadow: 5px 5px 0 #b015b0;
  margin-bottom: 0.75rem;
}
.service__group .service__item .service__detail {
  font-size: 1.5rem;
  font-weight: bold;
}
.service__group .service__item .service__description {
  font-size: 1.125rem;
  color: #333;
}
.service__group .service__item .service__link {
  position: absolute;
  background: transparent;
  color: white;
  font-size: 1.25rem;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.6s ease;
  /* Thay đổi transition cho link */
}
.service__group .service__item .service__link:hover {
  color: #000;
  font-weight: bold;
}
.service__group .service__item:last-child {
  border-right: none;
}
.service__group .service__item:nth-child(3) {
  border: none;
}
.service__group .service__item:hover {
  background-color: #d26cd2;
}
.service__group .service__item:hover .service__detail, .service__group .service__item:hover .service__description {
  color: white;
}
.service__group .service__item:hover .service__link {
  opacity: 1;
  visibility: visible;
}
.service__group .service--product .service__plus {
  box-shadow: 5px 5px 0 rgba(235, 170, 50, 0.8);
}
.service__group .service--product:hover {
  background: linear-gradient(135deg, #d94545, #f9a710);
}
.service__group .service--user .service__plus {
  box-shadow: 5px 5px 0 rgba(7, 247, 251, 0.8);
}
.service__group .service--user:hover {
  background: linear-gradient(135deg, #339191, aqua);
}
.service__group .service--seo {
  border-top: 1px solid purple;
}
.service__group .service--seo .service__plus {
  box-shadow: 5px 5px 0 #f506c5;
}
.service__group .service--seo:hover {
  background: linear-gradient(135deg, #f5a91e, #d60ad6);
}
.service__group .service--category {
  border-top: 1px solid purple;
}
.service__group .service--category .service__plus {
  box-shadow: 5px 5px 0 #e41a1a;
}
.service__group .service--category:hover {
  background: linear-gradient(135deg, #cc890c, #d60e0e);
}
.service__group .service--gift {
  border-top: 1px solid purple;
}
.service__group .service--gift .service__plus {
  box-shadow: 5px 5px 0 #a1f623;
}
.service__group .service--gift:hover {
  background: linear-gradient(135deg, #109110, #0df10d);
}
.service .submit {
  position: relative;
  width: 100%;
  border-radius: 1.5rem;
  margin-top: 4rem;
  display: flex;
  justify-content: center;
}
.service .submit .input {
  background: linear-gradient(135deg, #23caca, #e0b7be);
  border: none;
  color: #000;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 2.25rem;
  padding: 1.5rem;
  padding-right: 12.5rem;
  width: 40%;
}
.service .submit .input:focus-visible {
  outline: none;
}
.service .submit .subcribe {
  position: absolute;
  background: linear-gradient(45deg, #f1400a, #a86f07);
  top: -11%;
  right: 31%;
}

.footer {
  background: #000;
  color: whitesmoke;
  opacity: 0.9;
  display: flex;
  justify-content: space-around;
}
.footer__left {
  width: 30%;
  margin-left: 4rem;
}
.footer__content {
  width: 70%;
  margin: 4rem 3rem;
  line-height: 1.4;
}
.footer__content .footer__right {
  width: 300px;
  height: auto;
  margin-bottom: 2rem;
}
@media (max-width: 58.75em) {
  .footer__content .footer__right {
    width: 200px;
  }
}
@media (max-width: 39.8125em) {
  .footer__content .footer__right {
    width: 100px;
  }
}
.footer__content .footer__title {
  font-size: 1.375rem;
  font-weight: 800;
}
.footer__content .footer__contact {
  margin-top: 2.25rem;
  font-size: 1.25rem;
  display: flex;
  gap: 2rem;
}
.footer__content .footer__contact .afair {
  width: 50%;
}
.footer__content .footer__contact .afair__title {
  font-size: 2rem;
  margin-bottom: 2rem;
}
.footer__content .footer__contact .afair__group {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #333;
  padding: 1rem 0;
}
.footer__content .footer__contact .afair__group:hover {
  color: purple;
  transition: color 0.5s ease-in-out, border 0.5s ease;
  border-bottom: 1px solid purple;
}